export { MemoryOptimizer, memoryOptimizer } from './memory-optimizer'
import { memoryOptimizer } from './memory-optimizer'

// ÊÄßËÉΩÁõëÊéßÁ±ªÂûã
export interface PerformanceMetrics {
  // Core Web Vitals
  firstContentfulPaint: number
  largestContentfulPaint: number
  cumulativeLayoutShift: number
  firstInputDelay: number
  timeToInteractive: number
  totalBlockingTime: number
  
  // Ëá™ÂÆö‰πâÊåáÊ†á
  bundleSize: number
  memoryUsage: number
  renderTime: number
  apiResponseTime: number
}

// ÊÄßËÉΩÈòàÂÄºÈÖçÁΩÆ
export const PERFORMANCE_THRESHOLDS: any = {
  // LighthouseËØÑÂàÜÊ†áÂáÜ
  lighthouse: {
    good: 90,
    needsImprovement: 50,
    poor: 0
  },
  
  // Core Web VitalsÈòàÂÄº
  coreWebVitals: {
    lcp: { good: 2500, needsImprovement: 4000 },
    fid: { good: 100, needsImprovement: 300 },
    cls: { good: 0.1, needsImprovement: 0.25 }
  },
  
  // BundleÂ§ßÂ∞èÈòàÂÄº
  bundle: {
    maxSize: 500 * 1024, // 500KB
    maxChunkSize: 250 * 1024, // 250KB
    maxAssetSize: 100 * 1024 // 100KB
  },
  
  // ÂÜÖÂ≠ò‰ΩøÁî®ÈòàÂÄº
  memory: {
    maxUsage: 100 * 1024 * 1024, // 100MB
    leakThreshold: 5, // 5MB/minÂ¢ûÈïø
    gcThreshold: 0.8 // 80%‰ΩøÁî®Áéá
  }
}

// ÊÄßËÉΩ‰ºòÂåñÂ∑•ÂÖ∑ÈõÜÂêà
export class PerformanceToolkit {
  /**
   * Memory score thresholds and corresponding scores
   */
  private static readonly MEMORY_SCORE_RULES = [
    { threshold: 100, score: 50 },
    { threshold: 50, score: 75 },
    { threshold: 25, score: 90 }
  ] as const;

  private static readonly MB_IN_BYTES = 1024 * 1024;
  private static readonly DEFAULT_SCORE = 100;

  /**
   * Calculate memory score based on usage
   */
  private static getMemoryScore(usedBytes?: number): number {
    if (usedBytes == null || usedBytes < 0 || isNaN(usedBytes)) {
      return this.DEFAULT_SCORE;
    }
    
    const usageMB = usedBytes / this.MB_IN_BYTES;
    
    for (const rule of this.MEMORY_SCORE_RULES) {
      if (usageMB > rule.threshold) {
        return rule.score;
      }
    }
    
    return this.DEFAULT_SCORE;
  }

  /**
   * Get overall performance score
   */
  static async getOverallScore(): Promise<number> {
    try {
      // Bundle size score
      const bundleScore: number = bundleAnalyzer?.getPerformanceScore() ?? this.DEFAULT_SCORE;
      
      // Memory usage score
      const memoryReport = memoryOptimizer?.getMemoryReport() ?? { current: null };
      const memoryScore: number = memoryReport.current?.usedJSHeapSize !== undefined
        ? this.getMemoryScore(memoryReport.current.usedJSHeapSize)
        : this.DEFAULT_SCORE;
      
      // Web Vitals score
      let webVitalsScore: number = this.DEFAULT_SCORE;
      try {
        webVitalsScore = await this.getWebVitalsScore();
      } catch (error) {
        console.warn('Failed to get Web Vitals score:', error);
      }
      
      // Weighted average
      const overallScore: number = (bundleScore * 0.3 + memoryScore * 0.3 + webVitalsScore * 0.4);
      
      return Math.round(overallScore);
    } catch (error) {
      console.error('Ëé∑ÂèñÊÄßËÉΩËØÑÂàÜÂ§±Ë¥•:', error)
      return 0
    }
  }
  
  /**
   * Ëé∑ÂèñWeb VitalsËØÑÂàÜ
   */
  static async getWebVitalsScore(): Promise<number> {
    if (typeof window === 'undefined') return 100
    
    try {
      // ‰ΩøÁî®Performance ObserverËé∑ÂèñÁúüÂÆûÊåáÊ†á
      const metrics: any = await this.collectWebVitals()
      
      let score: any = 100
      
      // LCPËØÑÂàÜ
      if (metrics.lcp > PERFORMANCE_THRESHOLDS.coreWebVitals.lcp.needsImprovement) {
        score -= 30
      } else if (metrics.lcp > PERFORMANCE_THRESHOLDS.coreWebVitals.lcp.good) {
        score -= 15
      }
      
      // FIDËØÑÂàÜ
      if (metrics.fid > PERFORMANCE_THRESHOLDS.coreWebVitals.fid.needsImprovement) {
        score -= 30
      } else if (metrics.fid > PERFORMANCE_THRESHOLDS.coreWebVitals.fid.good) {
        score -= 15
      }
      
      // CLSËØÑÂàÜ
      if (metrics.cls > PERFORMANCE_THRESHOLDS.coreWebVitals.cls.needsImprovement) {
        score -= 40
      } else if (metrics.cls > PERFORMANCE_THRESHOLDS.coreWebVitals.cls.good) {
        score -= 20
      }
      
      return Math.max(0, score)
    } catch (error) {
      console.error('Ëé∑ÂèñWeb VitalsËØÑÂàÜÂ§±Ë¥•:', error)
      return 100 // ÈªòËÆ§Êª°ÂàÜ
    }
  }
  
  /**
   * Êî∂ÈõÜWeb VitalsÊåáÊ†á
   */
  static async collectWebVitals(): Promise<{
    lcp: number
    fid: number
    cls: number
    fcp: number
    ttfb: number
  }> {
    return new Promise((resolve) => {
      const metrics: any = {
        lcp: 0,
        fid: 0,
        cls: 0,
        fcp: 0,
        ttfb: 0
      }
      
      // Ê®°ÊãüÊåáÊ†áÊî∂ÈõÜ
      setTimeout(() => {
        // ‰ªéPerformance APIËé∑ÂèñÁúüÂÆûÊï∞ÊçÆ
        if ('performance' in window) {
          const navigation: any = performance.getEntriesByType('navigation')[0] as PerformanceNavigationTiming
          if (navigation) {
            metrics.ttfb = navigation.responseStart - navigation.requestStart
          }
          
          // Ëé∑ÂèñPaintÊåáÊ†á
          const paintEntries: any = performance.getEntriesByType('paint')
          const fcpEntry: any = paintEntries.find((entry: any) => entry.name === 'first-contentful-paint')
          if (fcpEntry) {
            metrics.fcp = fcpEntry.startTime
          }
        }
        
        // ‰ΩøÁî®PerformanceObserverËé∑ÂèñÂÖ∂‰ªñÊåáÊ†á
        if ('PerformanceObserver' in window) {
          try {
            // LCPËßÇÂØüÂô®
            const lcpObserver: any = new PerformanceObserver((list) => {
              const entries: any = list.getEntries()
              const lastEntry: any = entries[entries.length - 1] as any
              if (lastEntry) {
                metrics.lcp = lastEntry.startTime
              }
            })
            lcpObserver.observe({ entryTypes: ['largest-contentful-paint'] })
            
            // CLSËßÇÂØüÂô®
            const clsObserver: any = new PerformanceObserver((list) => {
              let clsValue: any = 0
              for (const entry of list.getEntries()) {
                if (!(entry as any).hadRecentInput) {
                  clsValue += (entry as any).value
                }
              }
              metrics.cls = clsValue
            })
            clsObserver.observe({ entryTypes: ['layout-shift'] })
            
            // FIDËßÇÂØüÂô®
            const fidObserver: any = new PerformanceObserver((list) => {
              for (const entry of list.getEntries()) {
                metrics.fid = (entry as any).processingStart - entry.startTime
              }
            })
            fidObserver.observe({ entryTypes: ['first-input'] })
            
          } catch (error) {
            console.warn('PerformanceObserver‰∏çÊîØÊåÅÊüê‰∫õÊåáÊ†á:', error)
          }
        }
        
        // ËÆæÁΩÆÈªòËÆ§ÂÄºÔºàÂ¶ÇÊûúÊó†Ê≥ïËé∑ÂèñÁúüÂÆûÊï∞ÊçÆÔºâ
        if (metrics.lcp === 0) metrics.lcp = 2000 // 2Áßí
        if (metrics.fcp === 0) metrics.fcp = 1500 // 1.5Áßí
        if (metrics.fid === 0) metrics.fid = 50 // 50ms
        if (metrics.cls === 0) metrics.cls = 0.05 // 0.05
        if (metrics.ttfb === 0) metrics.ttfb = 200 // 200ms
        
        resolve(metrics)
      }, 1000)
    })
  }
  
  /**
   * ÁîüÊàêÊÄßËÉΩ‰ºòÂåñÊä•Âëä
   */
  static async generatePerformanceReport(): Promise<string> {
    const overallScore: any = await this.getOverallScore()
    const bundleAnalysis: any = bundleAnalyzer?.getCurrentAnalysis() || {}
    const memoryReport: any = memoryOptimizer?.getMemoryReport() || { current: null, peak: null }
    const webVitals: any = await this.collectWebVitals()
    
    let report: any = `
# ÊÄßËÉΩ‰ºòÂåñÊä•Âëä

## ÊÄª‰ΩìËØÑÂàÜ: ${overallScore}/100

## Core Web Vitals
- **LCP (ÊúÄÂ§ßÂÜÖÂÆπÁªòÂà∂)**: ${webVitals.lcp.toFixed(0)}ms ${webVitals.lcp <= 2500 ? '‚úÖ' : webVitals.lcp <= 4000 ? '‚ö†Ô∏è' : '‚ùå'}
- **FID (È¶ñÊ¨°ËæìÂÖ•Âª∂Ëøü)**: ${webVitals.fid.toFixed(0)}ms ${webVitals.fid <= 100 ? '‚úÖ' : webVitals.fid <= 300 ? '‚ö†Ô∏è' : '‚ùå'}
- **CLS (Á¥ØÁßØÂ∏ÉÂ±ÄÂÅèÁßª)**: ${webVitals.cls.toFixed(3)} ${webVitals.cls <= 0.1 ? '‚úÖ' : webVitals.cls <= 0.25 ? '‚ö†Ô∏è' : '‚ùå'}
- **FCP (È¶ñÊ¨°ÂÜÖÂÆπÁªòÂà∂)**: ${webVitals.fcp.toFixed(0)}ms
- **TTFB (È¶ñÂ≠óËäÇÊó∂Èó¥)**: ${webVitals.ttfb.toFixed(0)}ms

## BundleÂàÜÊûê
${bundleAnalysis ? `
- **ÊÄªÂ§ßÂ∞è**: ${(bundleAnalysis.totalSize / 1024).toFixed(1)}KB
- **ÂéãÁº©Âêé**: ${(bundleAnalysis.gzippedSize / 1024).toFixed(1)}KB
- **‰ºòÂåñÂª∫ËÆÆ**: ${bundleAnalysis.recommendations.length}È°π
` : '- BundleÂàÜÊûêÊï∞ÊçÆ‰∏çÂèØÁî®'}

## ÂÜÖÂ≠ò‰ΩøÁî®
${memoryReport.current ? `
- **ÂΩìÂâç‰ΩøÁî®**: ${memoryOptimizer?.formatMemorySize(memoryReport.current.usedJSHeapSize) || 'N/A'}
- **Â≥∞ÂÄº‰ΩøÁî®**: ${memoryReport.peak ? memoryOptimizer?.formatMemorySize(memoryReport.peak.usedJSHeapSize) || 'N/A' : 'N/A'}
- **Ë∂ãÂäø**: ${memoryReport.trend === 'increasing' ? 'üìà Â¢ûÈïø' : memoryReport.trend === 'decreasing' ? 'üìâ ‰∏ãÈôç' : '‚û°Ô∏è Á®≥ÂÆö'}
- **‰ºòÂåñÂª∫ËÆÆ**: ${memoryReport.optimizations.length}È°π
` : '- ÂÜÖÂ≠òÁõëÊéßÊï∞ÊçÆ‰∏çÂèØÁî®'}

## ÊÄßËÉΩÁ≠âÁ∫ß
${overallScore >= 90 ? 'üü¢ ‰ºòÁßÄ (90-100ÂàÜ)' : 
  overallScore >= 75 ? 'üü° ËâØÂ•Ω (75-89ÂàÜ)' : 
  overallScore >= 60 ? 'üü† ÈúÄË¶ÅÊîπËøõ (60-74ÂàÜ)' : 
  'üî¥ ËæÉÂ∑Æ (0-59ÂàÜ)'}

## Âª∫ËÆÆ‰ºòÂÖàÁ∫ß
1. **È´ò‰ºòÂÖàÁ∫ß**: Core Web Vitals‰ºòÂåñ
2. **‰∏≠‰ºòÂÖàÁ∫ß**: BundleÂ§ßÂ∞è‰ºòÂåñ
3. **‰Ωé‰ºòÂÖàÁ∫ß**: ÂÜÖÂ≠ò‰ΩøÁî®‰ºòÂåñ

---
*Êä•ÂëäÁîüÊàêÊó∂Èó¥: ${new Date().toLocaleString()}*
`
    
    return report
  }
}

// ÂØºÂá∫ÊÄßËÉΩÂ∑•ÂÖ∑ÂåÖ
export { PerformanceToolkit as any }